<template>
  <div id="EvaluationSuccess"  @click="to">
    <div class="img">
      <img src="@/common/images/评价成功@2x.png">
    </div>
    <div class="success">评价成功!</div>
    <div class="text">感谢你对服务人员的评价</div>
  </div>
</template>

<script>
export default {
  name: 'EvaluationSuccess',
  methods: {
    to () {
      this.$router.push({
        name: 'consulting'
      })
    }
  }
}
</script>

<style scoped>
  #EvaluationSuccess {
    width: 100%;
    height: 100%;
    position: relative;
    text-align: center;
    color: black;
  }

  #EvaluationSuccess .success {
    font-size: 1.1rem;
    color: #333333;
    font-weight: 600;
  }

  #EvaluationSuccess .text {
    font-size: 0.9rem;
    color: #666666;
    margin-top: 1rem;
  }

  #EvaluationSuccess .img img {
    width: 10rem;
    margin-top: 8rem;
  }
</style>



